<template>
	<div class="index-lift clearfix" id="lift">
		<div class="button" v-for="item in title" :class="item.ti">
			<a class="breathe" href="javascript:void(0)" target="_blank">
				<span class="icon"></span>
				<span class="text">{{item.info.one}}<br>{{item.info.two}}</span>
			</a>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				title: [{
					ti: 'anchor-btn',
					info: {
						one: '我要',
						two: '开播'
					}
				}, {
					ti: 'help-btn',
					info: {
						one: '帮助',
						two: '中心'
					}
				}, {
					ti: 'code-btn',
					info: {
						one: '扫码',
						two: '直播'
					}
				}, {
					ti: 'service-btn',
					info: {
						one: '客服',
						two: '支持'
					}
				}],
			}
		}
	}
</script>

<style>

</style>


